import { graphql, useStaticQuery } from 'gatsby'

const usePaymentLogos = () => {
  const data = useStaticQuery(graphql`
    query {
      strapi {
        paymentTypes {
          name
          logo {
            url
            imageFile {
              publicURL
            }
          }
        }
      }
    }
  `)

  return data.strapi.paymentTypes.map(items => {
    return {
      name: items.name,
      logoSrc: items.logo[0].imageFile.publicURL,
    }
  })
}

export default usePaymentLogos
