/**
 * Formatting giving number in [Cents] to user readable price
 * @param priceStr
 * @returns {string} the price in readable price
 */
export const formatNumberToPrice = priceStr => {
  let priceInt = parseInt(priceStr)
  let formattedPrice = priceInt / 100

  return formattedPrice.toFixed(2)
}
