import React from 'react'
import { graphql } from 'gatsby'
import Layout from '../components/Layout'
import SEO from '../components/SEO'
import DynamicZones from '../components/DynamicZones'
import ProductIntro from '../components/ProductIntro'

export const settings = graphql`
  fragment settingOptions on safro_ComponentBlockSettingsBlockSettings {
    background
    blockId
  }
`

export const query = graphql`
  query($id: ID!) {
    strapi {
      product(id: $id) {
        name
        description
        notes {
          ... on safro_ComponentNoteNote {
            noteItem
          }
        }
        gallery {
          id
          url
          alternativeText
          imageFile {
            childImageSharp {
              fluid {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
        }
        thumbs: gallery {
          id
          url
          alternativeText
          imageFile {
            childImageSharp {
              fluid(maxWidth: 200, maxHeight: 200) {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
        }
        isVariable
        variableSizes {
          ... on safro_ComponentSizeVariablesVariableType {
            id
            size
            price
            shippingPrice
            onSale
            salePrice
          }
        }

        productZones {
          ... on safro_ComponentTextText {
            __typename
            settings {
              ...settingOptions
            }
            title
            text
            textAlign
            titleAlign
          }

          ... on safro_ComponentImageImage {
            __typename
            settings {
              ...settingOptions
            }
            caption
            imageAlt
            image {
              url
              imageFile {
                childImageSharp {
                  fluid {
                    ...GatsbyImageSharpFluid_withWebp
                  }
                }
              }
            }
          }
          ... on safro_ComponentQuoteQuote {
            __typename
            settings {
              ...settingOptions
            }
            blockTitle: title
            blockText: text
            quotes {
              quote
              image {
                alternativeText
                url
                imageFile {
                  childImageSharp {
                    fixed(width: 120, height: 120) {
                      ...GatsbyImageSharpFixed
                    }
                  }
                }
              }
              name
              profession
            }
          }
          ... on safro_ComponentTwoCalLayoutTwoColLayout {
            __typename
            settings {
              ...settingOptions
            }
            video
            media {
              mime
              alternativeText
              url
              imageFile {
                publicURL
                childImageSharp {
                  fluid {
                    ...GatsbyImageSharpFluid_withWebp
                  }
                }
              }
            }
            title
            text
            colReverse
          }
        }
      }
    }
  }
`

const Product = ({ data, pageContext }) => {
  const {
    strapi: {
      product: {
        productZones,
        name,
        description,
        notes,
        gallery,
        thumbs,
        isVariable,
        variableSizes,
      },
    },
  } = data

  return (
    <>
      <SEO title={pageContext.name} />
      <Layout hasNoHero={false}>
        <ProductIntro
          gallery={gallery}
          isVariable={isVariable}
          thumbs={thumbs}
          name={name}
          notes={notes}
          description={description}
          variableSizes={variableSizes}
        />
        <DynamicZones pageLayout={productZones} />
      </Layout>
    </>
  )
}

export default Product
