import React, { useRef } from 'react'
import PropTypes from 'prop-types'
import Img from 'gatsby-image'

// Styles
import styles from './gallery.module.scss'

const Gallery = ({ gallery, thumbs }) => {
  const galleryRef = useRef()

  const changeGalleryImage = event => {
    event.preventDefault()

    const images = [...galleryRef.current.children]
    images.map((img, key) => {
      if (img.id === event.target.id) {
        return (img.style.opacity = '1')
      } else {
        return (img.style.opacity = '0')
      }
    })
  }

  return (
    <section className={styles.gallery}>
      <div ref={galleryRef} className={styles.images}>
        {gallery &&
          gallery.map((img, key) => {
            return (
              <div key={key} id={img.id} className={styles.image}>
                <Img
                  fluid={img.imageFile.childImageSharp.fluid}
                  alt={img.alternativeText}
                />
              </div>
            )
          })}
      </div>
      <div className={styles.thumbs}>
        {thumbs &&
          thumbs.map((img, key) => {
            console.log(img.id)
            return (
              <a
                key={key}
                onClick={event => {
                  changeGalleryImage(event)
                }}
                id={img.id}
                className={styles.thumb}
              >
                <Img
                  fluid={img.imageFile.childImageSharp.fluid}
                  alt={img.alternativeText}
                />
              </a>
            )
          })}
      </div>
    </section>
  )
}

Gallery.propTypes = {
  gallery: PropTypes.array.isRequired,
  thumbs: PropTypes.array.isRequired,
}

export default Gallery
