import React, { useState } from 'react'
import PropTypes from 'prop-types'
import ReactMarkdown from 'react-markdown'

// Hooks
import usePaymentLogos from '../../hooks/usePaymentLogos'

// Helpers
import { formatNumberToPrice } from '../../helpers/prodcutHelpers'

// Components
import Block from '../Block'
import Slider from '@material-ui/core/Slider'
import Gallery from './Gallery'

// Styles
import styles from './product-intro.module.scss'
import Button from '../Button'

const ProductIntro = ({
  gallery,
  thumbs,
  name,
  description,
  notes,
  isVariable,
  variableSizes,
}) => {
  const [product, setProduct] = useState(variableSizes[0])
  const paymentTypes = usePaymentLogos()

  /**
   * Update product and state based on the selected product quantity
   * @param event
   * @param newValue
   */
  const updateProduct = (event, newValue) => {
    const currentProduct = variableSizes.filter(product => {
      return parseInt(product.size) === newValue
    })

    if (Number(product.size) !== newValue) {
      setProduct(currentProduct[0])
    }
  }

  // Calc can price form product price
  let productPrice = formatNumberToPrice(product.price)
  let pricePerCan = productPrice / parseInt(product.size)

  // Markers for the slider
  const sortMarksArray = variableSizes.map(variable => {
    return {
      value: variable.size,
    }
  })

  return (
    <Block className={styles.productIntro}>
      <Gallery thumbs={thumbs} gallery={gallery} />
      <section className={styles.introDetails}>
        <h1>{name}</h1>
        <ReactMarkdown>{description}</ReactMarkdown>
        <div className={styles.notes}>
          {notes &&
            notes.map((note, key) => {
              return (
                <p
                  className={styles.note}
                  key={key}
                  dangerouslySetInnerHTML={{ __html: note.noteItem }}
                />
              )
            })}
        </div>
        <div className={styles.price}>
          <h2 className={styles.priceTitle}>Price</h2>
          <p>{productPrice}</p>
          <p>price per can {pricePerCan.toFixed(2)} </p>
        </div>
        <div>
          <h2>
            Menge <span>{product.size} Dosen</span>
          </h2>
          <p>Hier ziehen um Menge auszuwählen</p>
          <Slider
            defaultValue={24}
            aria-labelledby="discrete-slider-small-steps"
            step={null}
            valueLabelDisplay="auto"
            marks={sortMarksArray}
            min={24}
            max={768}
            onChange={updateProduct}
            classes={{
              root: styles.slider,
              rail: styles.sliderRail,
              mark: styles.sliderMark,
              thumb: styles.sliderThumb,
            }}
          />
          <Button buttonText={'in den warnkorb'} slug={'/'} />
        </div>
        <div>
          {paymentTypes &&
            paymentTypes.map((item, key) => {
              return <img key={key} src={item.logoSrc} />
            })}
        </div>
      </section>
    </Block>
  )
}

ProductIntro.propTypes = {
  gallery: PropTypes.array.isRequired,
  name: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  notes: PropTypes.array.isRequired,
  isVariable: PropTypes.bool.isRequired,
  variableSizes: PropTypes.array,
}

ProductIntro.defaultProps = {
  variableSizes: [],
}

export default ProductIntro
